import {isValidURL} from "../../lib/src/index.js";
import {useEffect, useState} from "../../../pkg/react.js";
import {useStore} from "./hooks.js";
import {useLocalStorage} from "./useLocalStorage.js";
export const useBaseURL = () => {
  const store = useStore();
  const [serverUrlJson, setServerUrlJson] = useLocalStorage("serverUrl", store.getServerUrl());
  const [baseURL, setBaseURL] = useState(window?.location.origin);
  useEffect(() => {
    if (baseURL !== null) {
      if (isValidURL(serverUrlJson)) {
        setBaseURL(serverUrlJson);
      } else {
        store.handleError(new Error(`Invalid base URL: ${serverUrlJson}, defaulting to atomicdata.dev`));
        setBaseURL("https://atomicdata.dev");
      }
    }
  }, [serverUrlJson]);
  useEffect(() => {
    store.setServerUrl(baseURL);
  }, [baseURL]);
  return [baseURL, setServerUrlJson];
};
