import {useEffect, useState} from "../../../pkg/react.js";
import {Agent} from "../../lib/src/index.js";
import {useStore} from "./hooks.js";
import {useLocalStorage} from "./useLocalStorage.js";
const AGENT_LOCAL_STORAGE_KEY = "agent";
export const useCurrentAgent = () => {
  const [agentJSON, setAgentJSON] = useLocalStorage(AGENT_LOCAL_STORAGE_KEY, null);
  const store = useStore();
  const [stateAgent, setStateAgent] = useState(store.getAgent());
  function handleSetAgent(agent) {
    setAgentJSON(agent);
    setStateAgent(agent);
    store.setAgent(agent);
    return;
  }
  useEffect(() => {
    if (agentJSON && store.getAgent() == null && stateAgent == null) {
      handleSetAgent(agentJSON);
    }
  }, [agentJSON]);
  return [stateAgent, handleSetAgent];
};
export function initAgentFromLocalStorage() {
  const lsItem = localStorage.getItem(AGENT_LOCAL_STORAGE_KEY);
  if (lsItem == null) {
    return null;
  }
  const agentJSON = JSON.parse(lsItem);
  const agent = agentJSON && Agent.fromJSON(agentJSON);
  return agent;
}
