import Bugsnag from "../pkg/@bugsnag/js.js";
import BugsnagPluginReact from "../pkg/@bugsnag/plugin-react.js";
import React from "../pkg/react.js";
import {isDev} from "../config.js";
export function handleError(e) {
  console.error(e);
  if (!isDev) {
    Bugsnag.notify(e);
  }
}
export function handleWarning(e) {
  console.warn(e);
}
export function handleInfo(e) {
  console.info(e);
  if (!isDev) {
    Bugsnag.notify(e);
  }
}
export function initBugsnag(apiKey) {
  Bugsnag.start({
    apiKey,
    plugins: [new BugsnagPluginReact()],
    releaseStage: isDev() ? "development" : "production",
    enabledReleaseStages: ["production"],
    autoDetectErrors: !isDev()
  });
  return Bugsnag.getPlugin("react").createErrorBoundary(React);
}
